/*****************************
    Typography
*****************************/
body {
  font-family: "Montserrat", sans-serif;
  font-style: normal;
  font-size: 16px;
  line-height: 26px;
  color: #666666;
  overflow-x: hidden;
  font-weight: 400; }

a {
  outline: medium none !important;
  color: #F2A68D;
  transition: all 0.3s ease-in-out;
  text-decoration: none !important; }
  a:focus {
    color: #F2A68D;
    text-decoration: none !important; }
  a:hover {
    color: #F2A68D;
    text-decoration: none !important; }

input {
  outline: medium none !important;
  color: #F2A68D; }

h1, h2, h3, h4, h5, h6 {
  font-family: "Cormorant Garamond", serif;
  font-weight: normal;
  color: #131310;
  margin-top: 0px;
  margin-bottom: 0;
  font-weight: 600; }
  h1 a, h2 a, h3 a, h4 a, h5 a, h6 a {
    color: inherit; }

label {
  font-weight: normal; }

h1 {
  font-size: 80px;
  font-style: normal;
  line-height: 1em; }

h2 {
  font-size: 58px;
  line-height: 1em; }

h3 {
  font-size: 30px;
  line-height: 1em; }

h4 {
  font-size: 26px; }

h5 {
  font-size: 22px; }

h6 {
  font-size: 18px; }

p {
  line-height: 24px; }

*::-moz-selection {
  background: #F2A68D;
  color: #ffffff;
  text-shadow: none; }

::-moz-selection {
  background: #F2A68D;
  color: #ffffff;
  text-shadow: none; }

::selection {
  background: #F2A68D;
  color: #ffffff;
  text-shadow: none; }

/* container-fluid */
.container-fluid.container-space {
  padding: 0 100px; }

/* form-control */
.form-control {
  border: 1px solid #ebebeb;
  border-radius: 0px;
  height: 55px;
  padding: 10px 20px;
  box-shadow: none;
  font-size: 14px;
  font-weight: 500;
  color: #666666;
  background-clip: inherit !important; }
  .form-control:focus {
    box-shadow: none;
    border-color: #F2A68D; }

.form-control::-moz-placeholder {
  color: #3a4957; }

.form-control::-ms-input-placeholder {
  color: #3a4957; }

.form-control::-webkit-input-placeholder {
  color: #3a4957; }

textarea.form-control {
  height: auto; }

/* custom-file */
.custom-file {
  height: 50px; }
  .custom-file .custom-file-input {
    height: 50px;
    color: #131310;
    border-color: #ebebeb; }
    .custom-file .custom-file-input:focus {
      box-shadow: none;
      border-color: #F2A68D; }
  .custom-file .custom-file-label {
    height: 50px;
    font-size: 14px;
    font-weight: 600;
    color: #131310;
    border-color: #ebebeb;
    padding: 15px 90px 15px 20px; }
    .custom-file .custom-file-label:after {
      height: 48px;
      padding: 14px 25px; }

.custom-file-input:focus ~ .custom-file-label {
  box-shadow: none;
  border-color: #F2A68D; }

/* input-group-prepend */
.input-group-prepend .input-group-text, .input-group-append .input-group-text {
  width: 50px;
  background: #ffffff;
  border-color: #ebebeb;
  box-shadow: none;
  justify-content: center; }

/* checkbox */
.custom-control-input:checked ~ .custom-control-label:before {
  background: #F2A68D;
  border-color: #F2A68D; }

.custom-control-input:not(:disabled):active ~ .custom-control-label:before {
  background: transparent;
  border-color: transparent; }

.custom-control-input:focus ~ .custom-control-label:before {
  box-shadow: none; }

.custom-control-input:focus:not(:checked) ~ .custom-control-label:before {
  border-color: #ebebeb; }

.custom-control-label:before {
  top: 2px; }

.custom-control-label:after {
  top: 2px; }

.custom-checkbox .custom-control-label:before {
  border: 2px solid #fbfbfb; }

/* border  */
.border {
  border-color: #ebebeb !important; }

.border-top {
  border-top-color: #ebebeb !important; }

.border-left {
  border-left-color: #ebebeb !important; }

.border-right {
  border-right-color: #ebebeb !important; }

.border-bottom {
  border-bottom-color: #ebebeb !important; }

/* badge  */
.badge + .badge {
  margin-left: 6px; }

.badge-md {
  padding: 5px 8px;
  font-size: 13px;
  font-weight: normal; }

.badge-primary {
  background: #F2A68D; }

/* back-to-to */
.back-to-top a {
  display: flex;
  justify-content: center;
  align-items: center;
  position: fixed;
  bottom: 50px;
  right: 40px;
  width: 50px;
  height: 50px;
  margin: 0px;
  color: #ffffff;
  font-size: 18px;
  background: #F2A68D;
  transition: all 0.3s ease-in-out;
  z-index: 999; }
  .back-to-top a:hover {
    color: #ffffff;
    background: #131310; }
  .back-to-top a:focus {
    color: #ffffff; }

/* breadcrumb */
.breadcrumb {
  margin-top: 20px;
  background: transparent;
  font-family: "Cormorant Garamond", serif;
  font-weight: 500;
  border-radius: 0;
  display: inline-flex;
  padding: 14px 40px; }
  .breadcrumb .breadcrumb-item {
    padding-right: 5px;
    display: inline-block; }
    .breadcrumb .breadcrumb-item a {
      color: #ffffff; }
      .breadcrumb .breadcrumb-item a:hover {
        color: #F2A68D; }
    .breadcrumb .breadcrumb-item:before {
      color: #ffffff; }
  .breadcrumb .breadcrumb-item.active {
    color: #F2A68D; }

/* pagination */
.pagination .page-item .page-link {
  padding: 10px 18px;
  color: #666666;
  border-color: #ebebeb;
  margin: 0 4px;
  border-radius: 0px; }
  .pagination .page-item .page-link:focus {
    box-shadow: none; }
  .pagination .page-item .page-link:hover {
    background: #F2A68D;
    border-color: #F2A68D;
    color: #ffffff; }

.pagination .page-item:first-child .page-link {
  border-radius: 0px; }

.pagination .page-item.active .page-link {
  background: #F2A68D;
  border-color: #F2A68D;
  color: #ffffff; }

/* blockquote */
.blockquote {
  font-size: 16px;
  border-left: 10px solid #F2A68D;
  padding-left: 30px;
  font-style: italic;
  display: block; }

.blockquote-quote {
  position: relative;
  font-size: 18px;
  border-left: 0 solid #F2A68D;
  padding-left: 30px;
  font-style: italic;
  display: block;
  z-index: 1; }
  .blockquote-quote p {
    position: relative; }
  .blockquote-quote i {
    position: absolute;
    z-index: 0;
    top: 20px;
    left: 0;
    font-size: 60px;
    line-height: 60px;
    color: #fbfbfb; }

iframe {
  width: 100%; }

/* table */
.table {
  border-collapse: separate;
  border-spacing: 0 0px;
  font-size: 16px;
  font-weight: 600; }
  .table td {
    padding: 14px 20px; }
  .table th {
    padding: 14px 20px; }

.table-bordered td {
  border-color: #ebebeb; }

.table-bordered th {
  border-color: #ebebeb; }

.table-striped tbody tr:nth-of-type(odd) {
  background: #F3DCD4; }

.table td, .table th {
  vertical-align: middle; }

.table-skew {
  padding: 0 30px; }
  .table-skew thead {
    transform: skew(-20deg); }
    .table-skew thead tr {
      transform: skew(0deg); }
  .table-skew tr {
    transform: skew(-20deg); }
  .table-skew td {
    transform: skew(20deg); }
  .table-skew th {
    transform: skew(20deg); }

@media (max-width: 991px) {
  .form-control {
    height: 49px; } }

