
/*****************************
    Helper Classes
*****************************/
/*color*/
.text-primary {
  color: $primary !important;
}

.text-secondary {
  color: $secondary !important;
}

.text-white {
  color: $white;
}

.text-dark {
  color: $gray-8 !important;
}

.text-light {
  color: $gray-3 !important;
}

.text-success {
  color: $success !important;
}

.text-danger {
  color: $danger !important;
}

.text-primary-soft {
  color: rgba($primary, 0.5) !important;
}

.text-stroke-white {
  color: transparent !important;
  -webkit-text-stroke-width: 1px;
  -webkit-text-stroke-color: $white;
  display: block;
}

.text-stroke-primary {
    color: transparent !important;
  -webkit-text-stroke-width: 1px;
  -webkit-text-stroke-color: $primary;
  display: block;
}

.text-stroke-dark {
    color: transparent !important;
  -webkit-text-stroke-width: 1px;
  -webkit-text-stroke-color: $gray-9;
  display: block;
}

.skew {
  transform: skew(-20deg);
}
.skew-reverse {
  transform: skew(20deg);
  display: inline-block;
}

/*Background*/
.bg-primary {
  background: $primary !important;
}

.bg-dark {
  background: $black !important;
}

.bg-secondary {
  background: $secondary !important;
}

.bg-light {
  background: $bg-light !important;
}

.bg-facebook {
  background: #5d82d1 !important;
}
.bg-pinterest {
  background: #e13138 !important;
}
.bg-linkedin {
  background: #238cc8 !important;
}

.bg-white-soft {
 background: rgba($white, 0.1);
}

.bg-dark-soft {
 background: rgba($black, 0.1);
}

/*Page section margin padding*/
.space-ptb {
  padding: 100px 0;
}

.space-pt {
  padding: 100px 0 0;
}

.space-pb {
  padding: 0 0 100px;
}

.h-100vh {
  height: 100vh !important;
}


.make-appointment {
    padding-top: 130px;
    padding-bottom: 360px;
}

/* Sticky column */
.is-sticky {
  position: -webkit-sticky;
  position: sticky;
  top: 100px;
}

hr {
  border-top-color: rgba($white, 0.1);
}
hr.hr-dark {
  border-top-color: rgba($black, 0.1);
}

/*img holder*/
.bg-holder {
  background-size: cover;
  background-position: center center;
  background-repeat: no-repeat;
  width: 100%;
}

.bg-holder-bottom {
  background-size: contain;
  background-position: center bottom;
  background-repeat: no-repeat;
  width: 100%;
}

/* pre-loader */

#pre-loader {
  background-color: #ffffff;
  height: 100%;
  width: 100%;
  position: fixed;
  z-index: 1;
  margin-top: 0px;
  top: 0px;
  left: 0px;
  bottom: 0px;
  overflow: hidden !important;
  right: 0px;
  z-index: 999999; }

#pre-loader img {
  text-align: center;
  left: 0;
  position: absolute;
  right: 0;
  top: 50%;
  transform: translateY(-50%);
  -webkit-transform: translateY(-50%);
  -o-transform: translateY(-50%);
  -ms-transform: translateY(-50%);
  -moz-transform: translateY(-50%);
  z-index: 99;
  margin: 0 auto; }

  .faq-search {
      position: relative;

      .form-control {
        padding-right: 50px;
      }

      i {
        position: absolute;
        right: 13px;
        top: 0;
        background: inherit;
        color: $black;
        padding: 18px 18px;
        cursor: pointer;
        border-radius: 0 5px 5px 0px;
        color: $primary;
      }

    }


