.bg-overlay-left-100 {
  position: relative;
  &:before{
    position: absolute;
    content: "";
    top: 0;
    left: -100%;
    background-color:$primary;
    width: 100%;
    height: 100%;
  }
}

.bg-overlay-right-100 {
  position: relative;
  &:before{
    position: absolute;
    content: "";
    top: 0;
    right: -100%;
    background-color:$secondary;
    width: 100%;
    height: 100%;
  }
}

.bg-overlay-left {
  position: relative;
  &:before{
    position: absolute;
    content: "";
    top: -20px;
    left: 0;
    background-color:$secondary;
    width: 46%;
    height: calc(100% - -20px);
    transform: skew(-20deg);
    transform-origin: 0 0;
  }
}

.half-overlay-left {
  position: relative;
  &:before{
    position: absolute;
    content: "";
    top: -40px;
    background-color:$primary;
    width: 106%;
    height: 100%;
    right: -5%;
    transform: skew(-20deg);
  }
    &:after{
    position: absolute;
    content: "";
    top: -40px;
    background-color:$primary;
    width: 100%;
    height: 100%;
    right: 100%;
    z-index: -1;
    transform: skew(-20deg);
  }

}

.half-overlay-right {
  position: relative;
  &:before {
    position: absolute;
    content: "";
    top: 0;
    background-color:$secondary;
    width: 106%;
    left: -5%;
    height: 100%;
    z-index: -1;
    transform: skew(-20deg);
  }
   &:after {
    position: absolute;
    content: "";
    top: 0;
    background-color:$secondary;
    width: 100%;
    left: 100%;
    height: 100%;
    z-index: -1;
    transform: skew(-20deg);
  }
}

/*avatar*/
.avatar {
