}

.left-0 {
  left: 0;
}

.right-0 {
  right: 0;
}

.bottom-0 {
  bottom: 0;
}

.top-0 {
  top: 0;
}

.overflw-x-hidden {
  overflow-x: hidden;
 }

.overflw-y-hidden {
  overflow-y: hidden;
 }


.position-center {
  position: absolute;
  left: 0;
  right: 0;
  top: 50%;
  transform: translateY(-50%);
  display: inline-block;
  text-align: center;
}


.fill-primary {
  fill: $primary;
  fill-rule: evenodd;
  opacity: 0.3;
}

.fill-light {
  fill: $gray-1;
  fill-rule: evenodd;
}

.fill-secondary {
   fill: $secondary;
  fill-rule: evenodd;
  opacity: 0.6;
}

@media (min-width: 1200px){

  .container {
      max-width: 1300px;
  }

}

@media (max-width: 1599px){
  .bmi-form {
    overflow-x: hidden;
    padding-top: 40px;
    margin-top: -40px;
  }

}

@media (max-width: 991px){
  .bmi-form .col-lg-6.half-overlay-right,
  .bmi-form .col-lg-6.half-overlay-left {
    padding: 0 30px;
  }

  .bmi-form .half-overlay-left:after,
  .bmi-form .half-overlay-right:after {
    content: none;
  }

  .bmi-form {
    padding-top: 0px;
    margin-top: 40px;
  }
}

